<template>
  <div class="posts">
    <ul>
      <li class="post-list" v-for="post in posts">
        <img :src="post.author.avatar_url" alt>
        <span class="reply-count">
          <span class="count-replies">{{ post.reply_count }}</span>
          <span>/</span>
          <span class="count-visits">{{ post.visit_count }}</span>
        </span>
        <span
          class="def-class"
          :class="[{
              put_good: post.good,
              put_top: post.top
          }]"
        >{{ post | tabFormatter}}</span>
        <router-link
          class="title"
          :to="{path: `/article/${post.id}&${post.author.loginname}`
          }"
        >{{ post.title }}</router-link>
        <span class="item">{{ post.last_reply_at | formatDate}}</span>
      </li>
    </ul>
    <pagination @handle="renderPosts"></pagination>
  </div>
</template>

<script>
import pagination from "./pagination";
export default {
  components: {pagination},
  data() {
    return {
      posts: {}
    };
  },
  methods: {
    getData(page) {
      this.$http("https://cnodejs.org/api/v1/topics", {
        params: {
          page: page,
          limit: 20        
        }
      })
        .then(res => {
          this.posts = res.data.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    renderPosts(page) {
      this.getData(page)
    }
  },
  created() {
    this.getData(1);
  }
};
</script>

<style scoped>
.posts {
  max-width: 80%;
  margin: 10px auto;
  padding-bottom: 50px;
  background: #fff;
}
.post-list {
  display: flex;
  padding: 10px;
  align-items: center;
  border-bottom: 1px solid #eee;
}
.post-list:hover {
  background: #f5f5f5;
}
.post-list > * {
  margin-right: 10px;
}
.reply-count {
  font-size: 10px;
}
.count-replies {
  color: blueviolet;
}
.count-visits {
  color: #b4b4b4;
}
.def-class {
  color: #999;
  background: #e5e5e5;
  border-radius: 3px;
  font-size: 12px;
  padding: 2px 4px;
}
.post-list img {
  width: 30px;
  height: 30px;
}
.title {
  flex: 1;
  font-size: 16px;
  color: #000;
  text-decoration: none;
}
.title:hover {
  text-decoration: underline;
}
.title:visited {
  color: #a9a79f;
}
.item {
  color: #778087;
  font-size: 11px;
}
.put_good,
.put_top {
  background: #80bd01;
  color: #fff;
}
</style>
