import Vue from 'vue'
import Router from 'vue-router'
import postList from '../components/postList'
import Article from '../components/Article'
import userInfo from '../components/userInfo'
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            component: postList
        },
        {
            path: '/article/:id&:name',
            component: Article,
            name: 'article'
        },
        {
            path: '/userInfo/:name',
            component: userInfo
        }
    ]
})