<template>
  <div class="pagination">
    <button @click="firstPage">首页</button>
    <button @click="previPage">上一页</button>
    <button v-if="pageBtns[0] > 1">....</button>
    <button v-for="btn in pageBtns" :class="[{currentPage:btn == currentPage}]"
        @click="changePage(btn)"
    >{{ btn }}</button>
    <button>....</button>
    <button @click="nextPage">下一页</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pageBtns: [1, 2, 3, 4, 5],
      currentPage: 1
    };
  },
  methods: {
      changePage(pageNum) {
          if (pageNum === this.pageBtns[this.pageBtns.length - 1]) {
            // 点击最后一个
            this.pageBtns.shift();
            this.pageBtns.push(pageNum + 1)
          } else if (pageNum === this.pageBtns[0] && pageNum !== 1) {
              this.pageBtns.unshift(pageNum - 1)
              this.pageBtns.pop()
          }
          this.currentPage = pageNum
          this.$emit('handle', this.currentPage)
      },
      previPage() {
          if (this.currentPage === 1) return;
          this.changePage(--this.currentPage)
      },
      nextPage() {
           this.changePage(++this.currentPage)
      },
      firstPage() {
          this.pageBtns = [1, 2, 3, 4, 5]
          this.currentPage = 1
          this.$emit('handle', this.currentPage)
      }
  }
};
</script>

<style scoped>
.pagination {
  margin-top: 30px;
  background-color: white;
  padding: 6px 20px;
  border-radius: 5px;
  /*box-shadow: 0px 2px 9px #888888;*/
  border: 1px solid #888888;
}

button {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #778087;
  border-radius: 3px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 0 2px;
  width: 55px;
  height: 29px;
  vertical-align: middle;
}

.pagebtn {
  position: relative;
  bottom: 1px;
  width: 40px;
  margin: 0 4px;
}

.currentPage {
  color: white;
  background-color: #1f1b1b;
}
</style>
